-------------------------*/
:root {
  --border-width: 2px;
  --radius-small: 4px;
  --radius: 10px;
  --radius-big: 16px;
  --p-light: 300;
  --p-regular: 400;
  --p-medium: 500;
  --p-semi-bold: 600;
  --p-bold: 700;
  --p-extra-bold: 800;
  --transition: 0.3s;
  --transition-2: 0.5s;
  --transition-transform: transform .65s cubic-bezier(.23,1,.32,1);
  --font-primary: "DM Sans", sans-serif;
  --font-secondary: "DM Sans", sans-serif;
  --font-size-b1: 16px;
  --font-size-b2: 15px;
  --line-height-b1: 23px;
  --line-height-b2: 1.85;
  --h1: 50px;
  --h2: 38px;
  --h3: 35px;
  --h4: 32px;
  --h5: 24px;
  --h6: 18px;
  --h1-lineHeight: 1.2;
  --h2-lineHeight: 1.39;
  --h3-lineHeight: 1.43;
  --h4-lineHeight: 1.4;
  --h5-lineHeight: 1.45;
  --h6-lineHeight: 1.62;
  --color-body: #18181B;
  --bgcolorStart: #10a3a8;
  --bgcolorSecond: #7c32db;
  --bgcolorThird: #891C53;
}

.dark_theme {
  --color-white: #ffffff;
