/* progress-wrap */
.progress-wrap {
  position: fixed;
  right: 30px;
  bottom: 25px;
  height: 46px;
  width: 46px;
  cursor: pointer;
  display: block;
  border-radius: 50px;
  box-shadow: inset 0 0 0 2px rgba(92, 92, 98, 0.2);
  z-index: 99;
  opacity: 0;
  visibility: hidden;
  transform: translateY(15px);
  -webkit-transition: all 200ms linear;
  transition: all 200ms linear;
}
@media (min-width: 768px) {
  .progress-wrap {
    right: 50px;
    bottom: 50px;
  }
}
.progress-wrap svg.up-arrow {
  position: absolute;
  text-align: center;
  color: var(--color-white);
  left: 12px;
  top: 12px;
  cursor: pointer;
  display: block;
  z-index: 1;
  vertical-align: middle;
  font-weight: 400;
  font-variant: normal;
  text-transform: none;
  speak: none;
  -webkit-font-smoothing: all 200ms linear;
  -moz-osx-font-smoothing: all 200ms linear;
}
.progress-wrap.active-progress {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.progress-wrap svg.progress-circle path {
  fill: none;
  stroke: var(--color-gray);
  stroke-width: 4;
  box-sizing: border-box;
  -webkit-transition: all 200ms linear;
  transition: all 200ms linear;
}

