/*--------------------------------------------------
	02. Magic Cursor
---------------------------------------------------*/
@keyframes bgcolor-change-3x {
  0% {
    background: var(--bgcolorStart);
  }
  33% {
    background: var(--bgcolorSecond);
  }
  66% {
    background: var(--bgcolorThird);
  }
  100% {
    background: var(--bgcolorStart);
  }
}
#magic-cursor {
  position: absolute;
  left: 0;
  top: 0;
  width: 30px;
  height: 30px;
  pointer-events: none;
  z-index: 10000;
  -webkit-transition: opacity 0.2s ease-in-out 0.5s;
  transition: opacity 0.2s ease-in-out 0.5s;
}

.hidden-ball #magic-cursor {
  opacity: 0 !important;
  visibility: hidden;
}

#ball {
  position: fixed;
  z-index: 1000001;
  width: 40px;
  height: 40px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  margin: -5px 0 0 -5px;
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.8);
  border-radius: 80%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  pointer-events: none;
  backface-visibility: hidden;
  -webkit-transform-origin: center center;
  transform-origin: center center;
  transition: all 0.5s cubic-bezier(0.36, 0.03, 0, 0.91);
  transition-property: border, -webkit-transform;
  transition-property: transform, border;
  transition-property: transform, border, -webkit-transform;
}

#ball p {
  color: #fff;
  width: 33px;
  height: 16px;
  line-height: 16px;
  padding-top: 0px;
  padding-left: 5px;
  margin: 0;
  text-align: center;
  font-size: 8px;
  font-weight: 600;
  display: block;
  opacity: 1;
  transition: all 0.2s cubic-bezier(0.215, 0.61, 0.355, 1) 0s;
}

.light-content #ball p {
  color: #fff;
}

#ball p.first {
  padding-top: 5px;
}

#ball-loader {
  width: 100px;
  height: 100px;
  position: absolute;
  background-color: transparent;
  border-right: 4px solid transparent;
  border-bottom: 4px solid #999999;
  border-left: 4px solid transparent;
  border-top: 4px solid transparent;
  border-radius: 100px;
  box-sizing: border-box;
  opacity: 0;
  -webkit-transform: translate(-14px, -14px) rotate(0deg);
  transform: translate(-14px, -14px) rotate(0deg);
  -webkit-animation: rotating 0.8s ease-in-out infinite;
  animation: rotating 0.8s ease-in-out infinite;
  -webkit-transition: opacity 0s ease-in-out 0s;
  transition: opacity 0s ease-in-out 0s;
}

.light-content #ball-loader {
  border-bottom: 4px solid #999999;
}

.show-loader #ball-loader {
  opacity: 1;
  -webkit-transition: opacity 0.2s ease-in-out 0s;
  transition: opacity 0.2s ease-in-out 0s;
}

.disable-cursor #magic-cursor {
  display: none;
}

#magic-cursor {
  display: none;
}
@media (min-width: 992px) {
  #magic-cursor {
    display: block;
  }
}

.light-content .pointer {
  position: fixed;
  z-index: 1000000;
  width: 10px;
  height: 10px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  margin: -5px 0 0 -5px;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 80%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  pointer-events: none;
  backface-visibility: hidden;
  -webkit-transform-origin: center center;
  transform-origin: center center;
  transition: background 0.5s cubic-bezier(0.36, 0.03, 0, 0.91);
}

.light_theme .light-content .pointer {
  background: rgba(0, 0, 0, 0.8);
}
.light_theme .light-content #ball {
  border-color: rgba(0, 0, 0, 0.8);
}

@-webkit-keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
@keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
.fadeInDown {
