.stdl-section-hero::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0.4;
  width: 100%;
  height: 100%;
  background-image: url(../img/stdl-hero-bg-2.svg);
  background-repeat: no-repeat;
