  position: absolute;
  top: 0;
  left: 0%;
  gap: 25px;
  transform: translateX(0%);
  overflow: visible;
}
.hero-grid-items::before {
  content: "";
  display: block;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 25px;
  z-index: 200;
  background: linear-gradient(rgb(24, 24, 27), rgba(0, 0, 0, 0));
}
.hero-grid-items::after {
  content: "";
  display: block;
  position: absolute;
  top: auto;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 25px;
  z-index: 200;
  background: linear-gradient(rgba(0, 0, 0, 0), rgb(24, 24, 27));
}
.hero-grid-items .col {
