  margin-top: -60px;
  animation: scrollBottomToTop 10s linear infinite alternate;
}

@keyframes scrollTopToBottom {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes scrollBottomToTop {
